/* font declaration */
@font-face {
  font-family: pokemon-font;
  src: url(./font/pokemon-font/pokemon-hollow.ttf);
  font-style: normal;
  font-weight: normal;
}
@font-face {
	font-family: pokemon-font-solid;
	src: url(./font/pokemon-font/pokemon-solid.ttf);
	font-style: normal;
	font-weight: normal;
}

/* body styling */
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
#root {
  background-image: url(./img/pokemon-wallpaper.jpg);
  background-size: 100%;
}
.pokemon-body {
  padding-bottom: 40px;
}
h1 {
	color: white;
	font-family: pokemon-font;
	font-style: normal;
	font-weight: normal;
	font-size: 70px;
  margin-top: 0;
  padding-top: 60px;
}
h2, p {
  margin: 10px 0;
}
h2 {
  font-family: calibri;
  font-size: 19px;
}
p {
  font-size: 14px;
}
input[type="search"] {
  text-align: center;
  border: 3px solid seagreen;
  padding: 13px 29px;
  font-size: 15px;
  color: black;
  font-family: calibri;
  font-weight: 600;
}
.searchbox-section {
  position: relative;
}
.searchbox-section:before {
  content: '';
  position: absolute;
  background-color: seagreen;
  width: 7px;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
}
.pokemon-section-wrapper {
  width: 1180px;
  max-width: 100%;
  margin: 0 auto;
  text-align: center;
}
.pokemon-box {
  display: inline-block;
  padding: 10px;
  margin: 7px;
  border: 1px solid #e3e3e3;
  border-radius: 7px;
  width: 150px;
  max-width: 100%;    
  background-color: white;
  -webkit-box-shadow: 2px 1px 3px #e3e3e3;
          box-shadow: 2px 1px 3px #e3e3e3;
}
img {
  width: 120px;
  height: 111px;
}
.searchbox-section {
  display: inline-block;
  margin-bottom: 40px;
}
.footer {
    background-color: white;
    padding: 17px 0;
    text-align: center;
    font-family: calibri;
    letter-spacing: 1px;
}
.footer a {
  text-decoration: none;
  color: seagreen;
  font-weight: 800;
}
.footer a:hover {
    color: red;
}
.error-title {
  font-family: pokemon-font;
  color: white;
  font-size: 40px;
}


